/*стили параллакс-эффекта по движению курсора*/

.parallaxWrapper {
  position: relative;
  min-height: 100vh;
  width: 100%;
  background: rgba(255, 150, 146, 0.55);
}

#parallax {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.parallax__layer {
  width: 3000px;
  height: 100%;
  position: absolute;
  left: 50%;
  margin-left: -1500px;
}

.parallax__layer img {
  position: absolute;
  bottom: 0;
}

/*------------------------------------------*/

/*------------------------------------------*/

/*стили параллакс при скролле на чистом css*/

.parallaxSection {
  background: rgba(255, 150, 146, 0.55);
  position: relative;
  height: 100vh;
  z-index: 2000;
}

.parallaxBlock {
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  top: 0;
  right: 0;
  left: 50%;
  bottom: 0;
  margin-left: -1300px;
  height: 100vh;
  /*указываем перспективу*/
  perspective: 100px;
}

.parallaxScroll__layer {
  width: 3000px;
  position: absolute;
  top: 0;
  bottom: 0;
}

.parallaxScroll__layer img {
  position: absolute;
  bottom: 0;
  display: block;
}

.parallaxScroll__content {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  min-height: 50%;
  background: #2D112B;
  transform: translateZ(0);
}

.parallaxScroll__layer {
  /*удаление слоев друг от друга в перспективк*/
}

.parallaxScroll__layer:nth-child(1) {
  transform: translateZ(-300px) scale(5.1);
}

.parallaxScroll__layer:nth-child(2) {
  transform: translateZ(-250px) scale(4.4);
}

.parallaxScroll__layer:nth-child(3) {
  transform: translateZ(-200px) scale(3.7);
}

.parallaxScroll__layer:nth-child(4) {
  transform: translateZ(-150px) scale(3);
}

.parallaxScroll__layer:nth-child(5) {
  transform: translateZ(-100px) scale(2.3);
}

.parallaxScroll__layer:nth-child(6) {
  transform: translateZ(-50px) scale(1.6);
}

.parallaxScroll__layer:nth-child(7) {
  transform: translateZ(0) scale(1);
}

