/*------------------------------------------*/

/*прокручивание фона*/

.desert {
  position: absolute;
  left: 0;
  min-height: 100vh;
  width: 6112px;
  background: url('../img/sand.jpg') bottom left repeat-x;
  animation: bg 200s linear infinite;
}

@keyframes bg {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-3056px);
  }
}

