/*параллакс по скроллу с использованием JS*/

.parallaxScrollJs {
  height: 100vh;
  position: relative;
  min-height: 650px;
  overflow: hidden;
  font-size: 0;
}

.parallaxScrollJs__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.parallaxScrollJs__section {
  position: absolute;
  top: 30%;
  bottom: 0;
  left: 50px;
  right: 0;
  margin: 0 auto;
  text-align: center;
}

.parallaxScrollJs__text {
  font-family: Arial, sans-serif;
  color: #ffffff;
  font-size: 30px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 25% auto;
  max-width: 800px;
  text-align: center;
}

