/*svg*/

.content {
  text-align: center;
  padding-top: 100px;
}

#heisenberg {
  display: inline-block;
  width: 30%;
  stroke: #455a64;
  stroke-width: 4px;
  fill: none;
}

#heisenberg .group {
  stroke-dasharray: 1200;
  stroke-dashoffset: 1200;
}

