/*------------------------------------------*/

/*blur-эффект*/

.blur {
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.blur__background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-width: 900px;
}

.blur__img {
  display: block;
  max-width: 100%;
}

.blur__form {
  width: 100%;
  height: 100%;
  background: url("../img/water.jpg") center no-repeat;
  -webkit-filter: blur(10px);
  filter: blur(10px);
}

.blur__form-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 400px;
  margin-left: -150px;
  margin-top: -200px;
  overflow: hidden;
  border-radius: 10px;
  position: relative;
}

