  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}